.profileInfo {
    display: grid;
    grid-template-areas: p d;
    grid-template-columns: 2fr 10fr;


}

.profilePhoto {
    grid-areas: p;
}

.profilePhoto img {
    border-radius: 150px;
}

.mainPhoto{
    width: 300px;
}

.profileDescription {
    grid-areas: d;
    padding: 10px;
    min-height: 280px;
    width: 97%;
    background-color: white;

}

.profileDescription span {
    font-weight: bold;

}